.btn {
  width: 260px;
  height: 60px;
  border-radius: 25px;
  background-color: #28e22c;
  background-image: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.3), transparent);
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.3), transparent);
  cursor: pointer;
  display: block;
  margin: 0 auto 50px;
  color: #fff;
  text-decoration: none;
  -webkit-transform: skew(-6deg);
  -ms-transform: skew(-6deg);
  transform: skew(-6deg);
  text-align: center;
  line-height: 60px;
  font-size: 20px;
  font-weight: 700;
  position: relative;
}

.btn::after {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  bottom: -5px;
  right: -5px;
  border: 7px solid rgba(255, 255, 255, 0.4);
  border-radius: 25px;
}

.btn:hover {
  background-image: -webkit-linear-gradient(bottom, transparent, rgba(0, 0, 0, 0.3));
  background-image: linear-gradient(to top, transparent, rgba(0, 0, 0, 0.3));
}

