html,
body,
.all-wrapper,
.main-content {
  height: 100%;
}

body {
  overflow-x: hidden;
}

.container {
  padding: 0 60px;
}

.scaleable-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.site-header {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 140px;
  -webkit-transition: background .8s ease-in-out;
  transition: background .8s ease-in-out;
}

.site-header.dark__header {
  background: #000;
}

.site-header .container {
  position: relative;
}

.site-header__inner {
  position: relative;
}

.site-header .logo {
  position: absolute;
  left: 0;
  top: 27px;
}

.cart-and-menu {
  position: absolute;
  right: 0;
  color: #fff;
  top: 34px;
  z-index: 1;
}

.cart-and-menu .cart {
  display: inline-block;
  padding-left: 46px;
  background: url(../img/cart.png) left center no-repeat;
  font: 700 16px/30px "Roboto Slab";
  color: #fff;
  text-decoration: none;
}

.cart-and-menu .cart:hover {
  color: #3bd439;
}

.toggle-menu {
  width: 76px;
  height: 76px;
  background: transparent;
  border: none;
  outline: 0;
  display: inline-block;
  margin-left: 45px;
  vertical-align: middle;
  padding: 10px;
  cursor: pointer;
}

.toggle-menu:hover span {
  background: #3bd439;
}

.toggle-menu span {
  margin-bottom: 4px;
  display: block;
  height: 8px;
  background: #fff;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.toggle-menu span:last-child {
  margin-bottom: 0;
}

.toggle-menu.active span:first-child {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  margin-bottom: -8px;
}

.toggle-menu.active span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.toggle-menu.active span:last-child {
  display: none;
}

.main-nav {
  position: fixed;
  right: 0;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  width: 390px;
  padding: 180px 70px 0 0;
  -webkit-transform: translateX(390px);
  -ms-transform: translateX(390px);
  transform: translateX(390px);
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}

.main-nav.visible {
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
}

.main-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: right;
}

.main-nav ul li {
  margin-bottom: 30px;
}

.main-nav ul a {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.main-nav ul a:hover {
  color: #3bd439;
}

.main-nav ul li.active a {
  color: #3bd439;
}

