.skew__wrapper {
  overflow: hidden;
  width: 100%;
}

.skew__wrapper--inner {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -5%;
  height: 960px;
}

.skew__item {
  width: 30%;
