  margin: 0 5px;
}

.skew__item:nth-child(2) {
  width: 40%;
}

.skew__item-inner {
  display: block;
  width: 135%;
  margin-left: 50%;
  height: 100%;
  overflow: hidden;
}

.skew__item--overflow {
  height: 100%;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}

.skew__item-heading {
  position: absolute;
  top: 0;
  width: 100%;
  margin: 0;
  padding: 15px 20px 10px;
  background: rgba(24, 199, 26, 0.9);
  font: 60px "intro";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  z-index: 1;
}

.skew__item-text {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.skew__item-text--inner {
  background: rgba(24, 199, 26, 0.9);
  color: #fff;
  font: 700 16px "Roboto Slab";
  padding: 35px;
}

.skew__item-text p {
  -webkit-transform: skew(-6deg);
  -ms-transform: skew(-6deg);
  transform: skew(-6deg);
}

.skew-direction__left .skew__item {
  -webkit-transform: skew(6deg);
  -ms-transform: skew(6deg);
  transform: skew(6deg);
}

.skew-direction__left .skew__item-inner {
  -webkit-transform: skew(-6deg) translate(-50%);
  -ms-transform: skew(-6deg) translate(-50%);
  transform: skew(-6deg) translate(-50%);
}

.skew-direction__left .skew__item-aligner {
  -webkit-transform: skew(-6deg);
  -ms-transform: skew(-6deg);
  transform: skew(-6deg);
  display: block;
}

.skew-direction__left .skew__item:first-child .skew__item-aligner {
  margin-left: 100px;
}

.skew-direction__right .skew__item {
  -webkit-transform: skew(-6deg);
  -ms-transform: skew(-6deg);
  transform: skew(-6deg);
}

.skew-direction__right .skew__item-inner {
  -webkit-transform: skew(6deg) translate(-50%);
  -ms-transform: skew(6deg) translate(-50%);
  transform: skew(6deg) translate(-50%);
}

.skew-direction__right .skew__item-aligner {
  -webkit-transform: skew(6deg);
  -ms-transform: skew(6deg);
  transform: skew(6deg);
  display: block;
}

.skew-direction__right .skew__item:last-child .skew__item-aligner {
  margin-right: 100px;
}

